* {
  box-sizing: border-box;
}
body {
  font-family: $font-family-regular;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  background-color: #fff;
  color: $font-color;
}
.brandingColor {
  color: $color-highlight;
}
a {
  color: $color-highlight;
  text-decoration: none;
  font-family: $font-family-regular;
  &:hover {
    color: $color-highlight;
    text-decoration: underline;
  }
}
.image {
  width: 100%;
  height: auto;
}
.container {
  max-width: 1200px;
  @media (min-width: 400px) {
    width: 90%;
  }
}
.title-1 {
  font-weight: normal;
  letter-spacing: 0;
  margin-bottom: 0;
  font-size: 45px;
  line-height: 1.2;
  &:not(.has-subtitle) {
    margin-bottom: 1em;
  }
}
.title-2 {
  font-weight: normal;
  letter-spacing: 0;
  margin-bottom: 0;
  font-size: 25px;
  line-height: 1.2;
  @media (min-width: 550px) {
    font-size: 30px;
  }
  @media (min-width: 750px) {
    font-size: 36px;
  }
  &:not(.has-subtitle) {
    margin-bottom: 42px;
  }
}
.title-3 {
  font-size: 20px;
  line-height: 1.2;
  @media (min-width: 550px) {
    font-size: 24px;
  }
  @media (min-width: 750px) {
    font-size: 28px;
  }
}
.sub-title {
  font-weight: normal;
  letter-spacing: 0;
  margin-bottom: 0;
  font-family: $font-family-book;
  font-size: 18px;
  line-height: 1.65;
  margin-top: 12px;
  @media (min-width: 750px) {
    font-size: 20px;
  }
  &.has-small-margin-bottom {
    margin-bottom: 1.2em;
  }
  &.no-margin-bottom {
    margin-bottom: 0;
  }
}
b,
strong {
  font-family: var(--font-family-medium);
}
.is-centered {
  text-align: center;
}
a.btn {
  display: inline-block !important;
}
.btn {
  display: inline-block;
  position: relative;
  padding: 10px 16px !important;
  border-radius: 28px !important;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  font-family: $font-family-regular !important;
  font-size: 13px !important;
  line-height: 1.1 !important;
  vertical-align: middle !important;
  text-decoration: none !important;
  transition: all 0.5s !important;
  overflow: hidden;
}
.btn.primary {
  color: #fff !important;
  border: 1px solid $color-highlight !important;
  background-color: $color-highlight !important;
  &:hover {
    border: 1px solid $color-highlight !important;
    background: $color-highlight !important;
    color: #fff !important;
  }
}

.page {
  padding: 4rem 0;
  display: flex;
  flex-flow: column;
  align-items: self-start;
  font-family: $font-family-regular;
  h1,
  h2,
  h3,
  h4,
  h5 {
    margin: 1rem 0 0.5rem;
    font-family: $font-family-regular !important;
    color: #0b8062 !important;
  }
  h2 {
    font-size: 36px;
    padding-top: 0;
    margin: 0 0 0.4em;
  }
  h3 {
    font-size: 1.2em;
  }
  .page-title {
    display: block;
    background: none;
    border-bottom: none;
    padding: 0 !important;
    margin-bottom: 1rem;
    text-align: center;
  }
  .page-sub-title {
    display: block;
  }
  a.nav-link {
    font-family: BentonSans, sans-serif;
    line-height: 1 !important;
    font-size: 1.6rem;
    margin: 1rem 0;
    padding: 1rem;
    color: $color-highlight;
    border: 1px solid #d5755d;
    display: inline-block;
  }
  a.nav-link:hover {
    text-decoration: none;
    color: #fff;
    background-color: $color-highlight;
    border-color: #d5755d;
  }
  a.nav-link::after {
    display: none !important;
  }
}
